import React, { Component } from 'react';
import style from "./style.scss";
import { Fa } from 'mdbreact';
import classNames from 'classnames';

const icons = ['fab fa-github-alt', 'fab fa-star', 'fab fa-code-fork']

class GithubRepository extends Component {
  render() {
    const { repository } = this.props
    return (
      <div className={style.panel}>
        <div className={style.panelIcon}>
          <Fa icon={icons[0]} size='lg' />
        </div>
        <div className={style.panelContent}>
          <div className={style.contentHeader}>{repository.name}</div>
          <div 
            className={style.contentDetail}
            style={{
              overflow: 'hidden', 
              textOverflow: 'ellipsis', 
              WebkitBoxOrient: 'vertical', 
              display: '-webkit-box', 
              WebkitLineClamp: 2}}>
              {repository.detail}
          </div>
          <div className={style.contentFooter}>
            <div className={classNames(style.iconText, "mr-2")}>
              <Fa icon={icons[1]} className="mr-2"/>
              {repository.star}
            </div>
            <div className={classNames(style.iconText, "mr-2")}>
              <Fa icon={icons[2]} className="mr-2"/>
              {repository.codeFork}
            </div>
            <div className={style.iconText}>{repository.language}</div>
          </div>
        </div>
      </div>
    );
  }
}

export default GithubRepository;