import React, { Component } from 'react';
import style from "./style.scss";
import GithubRepository from './GithubRepository';
import { repositoriesData } from '../data';


class SocialConnect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showRepos: false
    }
    this.show = this.show.bind(this);
  }
  show() {
    this.setState({ showRepos: !this.state.showRepos });
  }
  render() {
    return (
      <div>
        <GithubRepository repository={repositoriesData[0]}/>
        <GithubRepository repository={repositoriesData[1]}/>
        {
            !this.state.showRepos 
          ? <div 
              className={style.controlViewMore}
              onClick={this.show}>
              view more
            </div>
          : <React.Fragment>
              {repositoriesData.map((repository, index)=>
                index > 1 &&
                <GithubRepository key={index} repository={repository} />
              )}
              <div 
                className={style.controlViewMore}
                onClick={this.show}>
                view less
              </div> 
            </React.Fragment>
        }

      </div>
    );
  }
}

export default SocialConnect;