import React, { Component } from 'react';
import style from "./style.scss";

class Endorsement extends Component {
  render() {
    const { endorser } = this.props
    return (
      <div className={style.endorsement}>
        <div className={style.endorserCount}>{endorser.count}</div>
        <div className={style.endorseCategory}>{endorser.category}</div>
        <div className={style.dashLine}></div>
        <div className={style.avatarList}>
          { 
            endorser.avatar.map((avatar,index)=>
              <div className={style.endorseAvatar} key={index}>
                <img  
                  className={style.imageAvatar} 
                  src={avatar.imgUrl}
                  alt={avatar.alt}/>
              </div>
          )}
        </div>
      </div>
    );
  }
}

export default Endorsement;