import React, { Component } from 'react';
import style from "./style.scss";
import { Link } from 'react-router-dom';
import classNames from 'classnames';

class TabBar extends Component {
  render() {
    const { category } = this.props;
    return (
      <div className={style.categoryPanel}>
        <div className={style.categoryIcon}>
          <img src={category.imgUrl} alt={category.name}/>
        </div>
        <div className={style.categoryContent}>
          <div className={classNames("d-flex align-items-center", style.contentHeader)}>
            <Link className={style.headerName}to='/'>{category.name}</Link>
            <div className={style.headerExperience}>{category.experience}</div>
          </div>
          <div className={style.contentDetail}>
            { category.detail.map((item, index)=>{
                if (index!==category.detail.length-1){
                  return (
                    <p key={index}>{item}</p>
                  )
                } else {
                  return (
                    <div key={index}>{item}</div>
                  )
                }
            })}
          </div>
          <div className={style.contentTag}>
            { category.tag.map((item, index)=>
                <Link to='/'key={index}>{item}</Link>)}
          </div>
        </div>
      </div>
    );
  }
}

export default TabBar;