import React, { Component } from 'react';
import style from "./style.scss";
import { Fa } from 'mdbreact';
import {badgeData} from '../data';

class StackoverflowStat extends Component {
  render() {
    return (
      <div className={style.panel} style={{height: 'auto'}}>
          <div>
            <Fa 
              className={style.panelIcon}
              icon='fab fa-stack-overflow' 
              size='lg'/>
          </div>
          <div className={style.contentHeader} style={{flex: 1}}>Reputation: 942</div>
          <div className={style.statBadge}>
            { 
              badgeData.map((item, index)=>
              <div key={index}>
                <Fa icon='fas fa-trophy' style={{color: `${item.color}`}} />
                <div className={style.badgeCount}>{item.count}</div>
              </div>)
            }
          </div>
        </div>
    );
  }
}

export default StackoverflowStat;